 /*slider */
 
.related_courses input[type=range] {
    -webkit-appearance: none;
    margin: 10px 0;
    width: 100%;
  }
  .related_courses input[type=range]:focus {outline: none;}
  .related_courses input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #E4E4E4;
    border-radius: 25px;
    border: 0px solid #000101;
  }
  .related_courses input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    border: 0px solid #000000;
    height: 10px;
    width: 80px;
    border-radius: 7px;
    background: #293B44;
    cursor: pointer;
    -webkit-appearance: none;
  }
  .related_courses input[type=range]:focus::-webkit-slider-runnable-track {
    background: #E4E4E4;
  }
  .related_courses input[type=range]::-moz-range-track {
    width: 100%;
    height: 12.8px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #E4E4E4;
    border-radius: 25px;
    border: 0px solid #000101;
  }
  .related_courses input[type=range]::-moz-range-thumb {
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    border: 0px solid #000000;
    height: 10px;
    width: 69px;
    border-radius: 7px;
    background: #237CAA;
    cursor: pointer;
  }
  .related_courses input[type=range]::-ms-track {
    width: 100%;
    height: 12.8px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    border-width: 39px 0;
    color: transparent;
  }
  .related_courses input[type=range]::-ms-fill-lower {
    background: #237CAA;
    border: 0px solid #237CAA;
    border-radius: 50px;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  }
  .related_courses input[type=range]::-ms-fill-upper {
    background: #237CAA;
    border: 0px solid #237CAA;
    border-radius: 50px;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  }
  .related_courses input[type=range]::-ms-thumb {
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    border: 0px solid #000000;
    height: 20px;
    width: 39px;
    border-radius: 7px;
    background: #237CAA;
    cursor: pointer;
  }
  .related_courses input[type=range]:focus::-ms-fill-lower {background: #237CAA;}
  
  .related_courses input[type=range]:focus::-ms-fill-upper {background: #237CAA;} 

